import { firebaseapp } from "../init"

export const fetchQuestions=async(db,no)=>{
    try{
        let val=await firebaseapp.firestore().collection(db).limit(no).get()
        if(!val.empty){
            let info=[]
            let docs=val.docs
            for(let i=0;i<docs.length;i++){
                info.push({id:docs[i].id,...docs[i].data()})
            }
            return{
                status:200,
                data:info
            }
        }else{
            return{
                status:200,
                data:[]
            }
        }
    }catch(err){
        return{
            status:400,
            message:err.message
        }
    }
}