import React,{useState} from 'react'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Fab from '@material-ui/core/Fab'
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import ClearIcon from '@material-ui/icons/Clear';
export default function QuestionSingle(props){
    const [correct,setCorrect]=useState("")
    const [disable,setDisable]=useState(false)
    const [first,setFirst]=useState(true)
    const submitAnswer=(str)=>{
        const {answer,score,setScore,attempt,setAttempt}=props
        if(answer===str){
            setScore(score+1)
        }
        setCorrect(props.answer)
        setFirst(false)
        setDisable(true)
        setAttempt(attempt+1)
    }
    return(
        <Grid item className="exam-grid-container">
            <Paper elevation={3}  className="card1">
            <div style={{display:"flex",flexDirection:"row",verticalAlign:"center"}}>
            <Fab title={props.index+1} style={{backgroundColor:"#ffd600",fontSize:22}}>{props.index+1}</Fab>
            <p style={{color:"white",fontSize:20,marginLeft:10}}>{props.text}</p>
            </div>
            <div style={{overflow:"auto"}}>
            {props.imageurl&&<img src={props.imageurl}/>}
            </div>
            <div className="exam-options-container">
            <Fab variant="extended" className={correct==="a"?"exam-options-success":
            first?"exam-options":"exam-options-error"}
            disabled={disable}
            onClick={()=>{
                submitAnswer("a")
            }}
            >
                Option a
                {correct==="a"?<CheckCircleOutlineIcon/>:first?<span></span>:<ClearIcon/>}
            </Fab> 
            <Fab variant="extended" className={correct==="b"?"exam-options-success":
            first?"exam-options":"exam-options-error"}
            disabled={disable}
            onClick={()=>{
                submitAnswer("b")
            }}
            >
                Option b
                {correct==="b"?<CheckCircleOutlineIcon/>:first?<span></span>:<ClearIcon/>}

            </Fab>
            <Fab variant="extended" className={correct==="c"?"exam-options-success":
            first?"exam-options":"exam-options-error"}
            disabled={disable}
            onClick={()=>{
                submitAnswer("c")
            }}
            >
                Option c
                {correct==="c"?<CheckCircleOutlineIcon/>:first?<span></span>:<ClearIcon/>}
            </Fab>
            <Fab variant="extended" className={correct==="d"?"exam-options-success":
            first?"exam-options":"exam-options-error"}
            disabled={disable}
            onClick={()=>{
                submitAnswer("d")
            }}>
                Option d
                {correct==="d"?<CheckCircleOutlineIcon/>:first?<span></span>:<ClearIcon/>}
            </Fab>
            </div> 
            </Paper>
        </Grid>
    );
}