import { firebaseapp } from "./../../firebase/init";

export class StorageManager {
  async uploadFile(file, db) {
    try {
      let response = await firebaseapp
        .storage()
        .ref(db + "/" + file.name)
        .put(file);
      let downloadUrl = await response.ref.getDownloadURL();
      return {
        success: true,
        name: file.name,
        downloadUrl: downloadUrl,
      };
    } catch {
      return {
        success: false,
      };
    }
  }
}
