import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export default function DialogWarning(props) {

    const {open,setOpen}=props

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Use Google's location service?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Hi I am Asim Roy Chowdhury. If you are feeling weak in mathematics 
            and improve your score better than now then feel free to contact me by 
            clicking the button contact me. 
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Disagree
          </Button>
          <a href="tel:919433255725" style={{textDecoration:"none"}}>
          <Button onClick={handleClose} color="primary" autoFocus>
            Contact me
          </Button></a>
        </DialogActions>
      </Dialog>
    </div>
  );
}