import Axios from "axios";

import { firebaseapp } from "../../firebase/init";
import jsCookie from "js-cookie";
import { url } from "../../url";
import { RESPONSE_VALID } from "../../constants/GeneralConstants";
export class AuthManager {
  async login(email, password) {
    let resp = await firebaseapp
      .auth()
      .signInWithEmailAndPassword(email, password);
    return {
      success: resp.user.uid != null,
      uid: resp.user.uid,
    };
  }
  async fetchAuthResponse() {
    let defaultResp = {
      isAdmin: false,
      isLoggedIn: false,
    };
    try {
      let jwt = jsCookie.get("userjwt");
      if (jwt == null || jwt == undefined) {
        return defaultResp;
      }
      let res = await Axios.get(url + "/auth/checkJWT/" + jwt);
      return {
        isAdmin: res?.data?.admin ?? defaultResp.isAdmin,
        isLoggedIn: res?.data?.message === RESPONSE_VALID,
      };
    } catch (err) {
      return defaultResp;
    }
  }
}
