import React, { useState, useEffect } from "react";
import "./style.css";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import { useDependency } from "../../hooks/useDependency";
export default function AllQuestionsSection(props) {
  const [data, setData] = useState([]);
  const { db } = props;
  const { questionInteractor } = useDependency();
  useEffect(() => {
    questionInteractor.fetchAllQuestion(db, setData);
  }, [db]);

  const deleteSingle = async (id) => {
    await questionInteractor.deleteAndFetchQuestion(db, id, setData);
  };

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        <Grid container spacing={2}>
          {data.map((item, index) => {
            return (
              <Grid item style={{ width: "100%" }}>
                <Paper elevation={3} className="card1">
                  <p style={{ color: "white", fontSize: 20 }}>{item.text}</p>
                  <div style={{ overflow: "auto" }}>
                    {item.imageurl && (
                      <a href={item.imageurl} data-lightbox="roadtrip">
                        <img src={item.imageurl} />
                      </a>
                    )}
                  </div>
                  <p style={{ color: "white", fontSize: 20 }}>
                    Answer: {item.answer}
                  </p>
                  <Button
                    variant="contained"
                    onClick={() => {
                      deleteSingle(item.id);
                    }}
                    style={{
                      backgroundColor: "white",
                      color: "#740e3f",
                      margin: 10,
                    }}
                  >
                    Delete
                  </Button>
                </Paper>
              </Grid>
            );
          })}
        </Grid>
      </Grid>
    </Grid>
  );
}
