import React,{useContext} from 'react'
import './style.css'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles';
import { AuthContext } from '../../context/auth'
import {useHistory} from 'react-router-dom'
const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      height: 140,
      width: 100,
    },
    control: {
      padding: theme.spacing(2),
    },
  }));
export default function UserTemplate(){
    const classes = useStyles();
    const [auth,setAuth]=useContext(AuthContext)
    const history=useHistory()
    return(
        <div>
            <Grid container className={classes.root} spacing={2}>
                <Grid item xs={12}>
                    <Grid container justify="center" spacing={2}>
                    <Grid item>
                    <Paper elevation={3} className="card1">
                        <div style={{paddingLeft:20,paddingRight:20}}>
                        <p className="cardHeading">Practice for 11 joint examination</p>
                        <p className="cardText">You are a {auth.isLoggedIn?"student of Asim roy chowdhury":"normal user"}</p>
                        <div style={{display:"flex",justifyContent:"space-between"}}>
                        <p className="cardText">No of question: </p>
                        <p className="cardText">{auth.isLoggedIn?20:10}</p>
                        </div>
                        <Button variant="contained" style={{backgroundColor:"white",color:"#c71d6f",margin:10}}
                        onClick={()=>{
                            history.push("/practice/exam/joint11practice")
                        }}
                        >
                        Procced to test youself</Button>
                        </div>
                    </Paper>
                    </Grid>

                    <Grid item>
                    <Paper elevation={3} className="card2">
                        <div style={{paddingLeft:20,paddingRight:20}}>
                        <p className="cardHeading">Practice for 11 joint examination</p>
                        <p className="cardText">You are a {auth.isLoggedIn?"student of Asim roy chowdhury":"normal user"}</p>
                        <div style={{display:"flex",justifyContent:"space-between"}}>
                        <p className="cardText">No of question: </p>
                        <p className="cardText">{auth.isLoggedIn?20:10}</p>
                        </div>
                        <Button variant="contained" style={{backgroundColor:"white",color:"#0045ff",margin:10}}
                            onClick={()=>{history.push("/practice/exam/joint12practice")}}
                        >
                        Procced to test youself</Button>
                        </div>
                    </Paper>
                    </Grid>

                    <Grid item>
                    <Paper elevation={3} className="card3">
                        <div style={{paddingLeft:20,paddingRight:20}}>
                        <p className="cardHeading">Practice for 11 joint examination</p>
                        <p className="cardText">You are a {auth.isLoggedIn?"student of Asim roy chowdhury":"normal user"}</p>
                        <div style={{display:"flex",justifyContent:"space-between"}}>
                        <p className="cardText">No of question: </p>
                        <p className="cardText">{auth.isLoggedIn?20:10}</p>
                        </div>
                        <Button variant="contained" style={{backgroundColor:"white",color:"#5f970b",margin:10}}
                        onClick={()=>{
                            history.push("/practice/exam/joint11and12practice")
                        }}
                        >
                        Procced to test youself</Button>
                        </div>
                    </Paper>
                    </Grid>
                </Grid>
                </Grid>
            </Grid>
        </div>
    );
}