export class QuestionInteractors {
  constructor(storage, dbManager) {
    this.storage = storage;
    this.dbManager = dbManager;
  }
  async upload(text, file, answer, db) {
    const answers = ["a", "b", "c", "d"];
    let str = "";
    let imagepath = "";
    if (!answers.includes(answer.toLowerCase())) {
      return {
        status: 400,
        message: "Answer should be in between a,b,c,d",
      };
    }
    if (file) {
      let resp = await this.storage.uploadFile(file, db);
      if (resp.success) {
        imagepath = `${db}/${resp.name}`;
      }
      return {
        status: 400,
        message: "Unable to upload file",
      };
    }
    let response = await this.dbManager.add(db, {
      text,
      imageurl: str,
      answer: answer.toLowerCase(),
      imagepath,
    });
    if (response.success) {
      return {
        status: 200,
        message: "Successfully done",
      };
    } else {
      return {
        status: 400,
        message: "Database error",
      };
    }
  }

  fetchAllQuestion(db, setData) {
    this.dbManager.addSnapShotCallBack(db, (docItem) => {
      setData(docItem);
    });
  }
  async deleteAndFetchQuestion(db, id, setData) {
    let response = await this.dbManager.delete(db, id);
    if (response.success) {
      this.fetchAllQuestion(db, setData);
    }
  }
}
