import React, { useContext, useEffect,useState } from 'react'
import './exam.css'
import {useParams} from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import { AuthContext } from '../../context/auth';
import { fetchQuestions } from '../../firebase/user';
import QuestionSingle from './questionSingle'
import Header from '../header/index'
import DialogWarning from './dialog'
export default function PracticeExam(props){
    const {db} =useParams()
    const [auth,seAuth]=useContext(AuthContext)
    const [data,setData]=useState([])
    const [error,setError]=useState("")
    const [score,setScore]=useState(0)
    const [attempt,setAttempt]=useState(0)
    const [open,setOpen]=useState(false)
    useEffect(()=>{
        fetch()
    },[])
    useEffect(()=>{
        if(!auth.isLoggedIn){
            if(attempt===10){
                let per=(score/10)*100
                if(per<80){
                    setOpen(true)
                }
            }
        }
    },[attempt])
    const fetch=async()=>{
        let res=await fetchQuestions(db,auth.isLoggedIn?20:10)
        if(res.status===200){
            setData(res.data)
        }else{
            setError(res.message)
        }
    }
    return(
        <React.Fragment>
        <DialogWarning open={open} setOpen={setOpen}/>
        <Header/>
        <div style={{width:"100%",height:50,display:"flex",justifyContent:"center"}}>
        <p style={{fontSize:20,color:"gray"}}>Score: {score}/{auth.isLoggedIn?20:10}</p>
        </div>
        <Grid item xs={12}>
            <Grid container spacing={2} justify="center">
            {data.map((item,index)=>{
                    return(
                        <QuestionSingle 
                        index={index}
                        key={index}
                        text={item.text} 
                        imageurl={item.imageurl} 
                        id={item.id}
                        answer={item.answer}
                        score={score}
                        setScore={setScore}
                        attempt={attempt}
                        setAttempt={setAttempt}
                        />
                    );
                })}
            </Grid>
        </Grid>
        </React.Fragment>
    );
}