import React, { createContext, useState } from "react";
import AuthInteractor from "../interactors/authInteractor";
import { QuestionInteractors } from "../interactors/questionInteractors";
import { AuthManager } from "../managers/auth/index";
import { StorageManager } from "../managers/storage/StorageManager";
import { DatabaseManager } from "../managers/db/DatabaseManager";
let data = {
  auth: {
    interactor: new AuthInteractor(new AuthManager()),
  },
  question: {
    interactor: new QuestionInteractors(
      new StorageManager(),
      new DatabaseManager()
    ),
  },
};
export const DependencyContext = createContext();

const DependencyProvider = (props) => {
  return (
    <DependencyContext.Provider value={data}>
      {props.children}
    </DependencyContext.Provider>
  );
};

export default DependencyProvider;
