import { firebaseapp } from "./../../firebase/init";

export class DatabaseManager {
  async add(db, obj) {
    try {
      let resp = await firebaseapp.firestore().collection(db).add(obj);
      return {
        success: true,
        id: resp.id,
      };
    } catch {
      return {
        success: false,
      };
    }
  }
  async addSnapShotCallBack(db, cb) {
    firebaseapp
      .firestore()
      .collection(db)
      .onSnapshot((snap) => {
        if (snap.empty) {
          cb([]);
        } else {
          cb(
            snap.docs.map((docItem) => {
              return {
                id: docItem.id,
                ...docItem.data(),
              };
            })
          );
        }
      });
  }

  async delete(db, id) {
    try {
      await firebaseapp.firestore().collection(db).doc(id).delete();
      return {
        success: true,
      };
    } catch {
      return {
        success: false,
      };
    }
  }
}
